<template>
    <div class="h-full transition-all w-80 border-r border-gray-200 dark:bg-gray-700" :class="'story--fields--'+ randomId">
        <Fields :fields="fields" :data="settings" :freeform="true" />
    </div>
</template>
  
<script lang="ts" setup>
import { reactive, provide } from 'vue';
// Generate random id
const randomId = Math.random().toString(36).substr(2, 9);
const props = withDefaults(defineProps<{
    /**
     * The label of the button
     */
    name: String,
    label: String,
    type: String,
    options?: Object,
    description?: String,
    placeholder?: String,
    value?:String | Object



}>(), {
        type: 'text',
        label: 'Text',
        placeholder: '',
        value: '',
        name: 'text',
        description: ''
});
provide(/* key */ 'teleport_id', /* value */ '.story--fields--'+ randomId) 
const fields = reactive(
    [{
        params: [props]
    }]
);
var settings = reactive({
    text: props.value
});



</script>